<template>
    <div class="read-text-page">
        <!-- <van-nav-bar
            class="nav-bar"
            :title="title"
            :border="false"
            left-arrow
            @click-left="onBack"
        /> -->
        <navigation-bar  @onLeftClick="onBack" :pageName="title" ></navigation-bar>

        <div class="read-content" v-html="detailHtml"></div>
    </div>
</template>

<script>

import {
    getProtocolService,
} from "../../services/loginService";

import Vue from 'vue';
import {Toast} from 'vant';
Vue.use(Toast);
import NavigationBar from '@/common/currency/NavigationBar.vue';

export default {
    components:{
        NavigationBar
    },
    name: "readTextPage",
    mounted () {
        // 获取传参
        let routerParams = this.$route.params;
        if (routerParams != undefined
        && routerParams.pageType != undefined) {
            this.pageType = routerParams.pageType;
        }
        // 获取页面数据
        this.getReadText();
    },
    data() {
        return {
            pageType: 1,
            title: '信我家服务协议',
            detailHtml: '',
        };
    },

    methods: {
        // 过滤style标签内容
        filterHtml: function (html) {
            var rel = /style\s*?=\s*?([‘"])[\s\S]*?\1/;
            var newHtml = '';
            if (html) {
                newHtml = html.replace(new RegExp(rel,'g'), '');
            }
            return newHtml;
        },
        onBack() {
            this.$router.back(-1);
        },
        // 加载关于信息
        getReadText() {
            let reqData = {
                PROTOCOL_CODE: "1",
            };
            if (1 == this.pageType) {
                //查看海信服务协议
                reqData.PROTOCOL_CODE = "1";
                this.title = "信我家服务协议";
            } else if (3 == this.pageType) {
                //查看隐私保护协议
                reqData.PROTOCOL_CODE = "3";
                this.title = "隐私保护协议";
            } else if (5 == this.pageType) {
                //查看隐私保护协议
                reqData.PROTOCOL_CODE = "5";
                this.title = "积分说明";
            }
            getProtocolService(reqData)
            .then(noteData => {
                let htmlStr = noteData['CONTENT'];
                this.detailHtml = htmlStr;
                // this.detailHtml = this.filterHtml(htmlStr);
            })
            .catch(error => {
                Toast(error);
            });
        }
    }
  };
</script>

<style scoped lang="scss">
    $navHeight: 44px;
    .read-text-page {
        box-sizing: border-box;
        position: absolute;
        overflow: hidden;;
        width: 100%;
        height: 100%;
        background-color: #f4f4f4;
        display: flex;
        flex-direction: column;
    }
    /* 导航 */
    .navBar {
        position: relative;
        width: 100%;
        height: $navHeight;
        z-index: 66;
    }

    .read-content {
        padding: 10px;
        font-family: "-apple-system", "Helvetica Neue", "Roboto", "Segoe UI", sans-serif;
        box-sizing: border-box;
        font-size: 14px;
        background-color: #fff;
        color: #333;
        text-align: left;
        width: 100%;
        overflow: auto;
        flex: 1;
    }
</style>
